.we-are {
  position: relative;
  padding-bottom: 241px;

  &__block {
    &-title {
      margin-bottom: 49px;
    }
  }

  &__content {
    display: flex;
    gap: 30px;

    &-left {
      max-width: 538px;
    }

    &-right {
      max-width: 539px;
    }

    &-title {
      margin: 0;
      margin-bottom: 24px;
      font-family: var(--second-family);
      font-weight: 700;
      font-size: 24px;
      line-height: 100%;
      color: var(--white);
    }

    &-descr {
      margin: 0;
      margin-bottom: 78px;
      font-weight: 500;
      font-size: 17px;
      line-height: 150%;
      color: var(--text-color);

      &--min {
        margin-bottom: 51px;
      }
    }

    &-rating {
      display: flex;
      align-items: center;
      gap: 16px;
    }

    &-rev {
      display: flex;
      flex-direction: column;
      gap: 8px;

      span {
        font-weight: 500;
        font-size: 14px;
        line-height: 100%;
        color: var(--text-color);
      }
    }

    &-stars {
      display: flex;
      gap: 5px;
    }
  }

  &__link {
    position: absolute;
    right: 72px;
    bottom: 174px;

    img {
      animation: spin 5s linear 0s infinite;
    }
  }
}

.video-container {
  position: relative;
  width: 348px;
  height: 348px;
  overflow: hidden;
  border-radius: 50%; /* Делает контейнер круглым */
  z-index: 5;

  &::after {
    content: '';
    position: absolute;
    top: 20px;
    left: 20px;
    border: 1px solid var(--yellow);
    border-radius: 220px;
    width: 308px;
    height: 308px;
    opacity: 0.3;
    z-index: 3;
    transition: opacity 0.3s ease;
  }
}

.video {
  width: 100%;
  height: 100%;
  border-radius: 50%; /* Обрезаем видео, чтобы оно было круглым */
  object-fit: cover; /* Заполняем контейнер видео */
  display: none; /* Скрываем видео по умолчанию */
}

.preview {
  width: 100%;
  height: 100%;
  border-radius: 50%; /* Обрезаем превью, чтобы оно было круглым */
  object-fit: cover; /* Заполняем контейнер превью */
  position: absolute;
  top: 0;
  left: 0;
  z-index: 3; /* Слои, чтобы превью было выше видео */
}

.play-button {
  position: absolute;
  top: 50%;
  left: 52%;
  transform: translate(-50%, -50%);
  width: 32px; /* Установи размер кнопки */
  height: 42px; /* Установи размер кнопки */
  background: url('../img/play.svg') no-repeat center center;
  background-size: contain;
  border: none;
  z-index: 4; /* Чтобы кнопка была поверх превью */
}

.video-container::after {
  opacity: var(--after-opacity, 0.3); /* Используем переменную, значение по умолчанию 0.3 */
}
