.meda-hero {
  position: relative;
  padding-bottom: 72px;
  height: 980px;
  background-image: url(../img/cases/meda-img.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  &__block {
    padding-top: 163px;

    &-link {
      position: relative;
      display: flex;
      align-items: center;
      gap: 15px;
      font-weight: 500;
      font-size: 18px;
      line-height: 100%;
      color: var(--white);
    }
  }

  &__folder {
    margin-bottom: 35px;
  }

  &__title {
    margin: 0;
    margin-bottom: 16px;
    font-weight: 500;
    font-size: 100px;
    line-height: 130%;
    color: var(--white);
  }

  &__descr {
    margin: 0;
    margin-bottom: 52px;
    max-width: 730px;
    font-weight: 500;
    font-size: 20px;
    line-height: 150%;
    color: var(--white);
  }

  &__link {
    bottom: 68px;
    z-index: 5;
  }
}
