.footer {
  &__block {
    padding-bottom: 52px;

    &-copy {
      display: flex;
      gap: 100px;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 32px;
    margin-bottom: 100px;
    max-width: 541px;
  }

  &__link {
    position: relative;
    font-family: var(--second-family);
    font-size: 52px;
    line-height: 100%;
    color: var(--white);
    transition: color .5s ease;
  }

  &__link:hover {
    background: linear-gradient(to left, yellow 50%, var(--white) 75%);
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
  }

  &__bot {
    margin: 0;
    font-weight: 500;
    font-size: 15px;
    line-height: 100%;
    color: #3f3f3f;
  }

  &__privacy {
    transition: color .5s ease-in-out;

    &:hover {
      color: var(--yellow);
    }
  }
}
