.career-hero {
  position: relative;
  padding-bottom: 133px;

  &__block {
    padding-top: 171px;

    &-top {
      display: flex;
      gap: 183px;
      margin-bottom: 19px;
    }

    &-subtitle {
      padding-top: 5px;
    }

    &-position {
      display: flex;
      align-items: center;
      gap: 10px;
      padding-left: 30px;
    }

    &-details {
      display: flex;
      align-items: center;
      gap: 30px;
    }
  }

  &__interested {
    margin: 0;
    margin-bottom: 26px;
    font-weight: 600;
    font-size: 24px;
    line-height: 150%;
    color: var(--white);
  }

  &__descr {
    margin: 0;
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    color: var(--white);
  }

  &__title {
    margin: 0;
    margin-bottom: 51px;
    font-weight: 600;
    font-size: 32px;
    line-height: 100%;
    color: var(--white);
  }

  &__vacancie {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid var(--black);

    &:not(:last-child) {
      margin-bottom: 28px;
    }
  }

  &__position {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1px 4px;
    font-weight: 600;
    font-size: 12px;
    color: #000;
    background: var(--white);
    text-transform: uppercase;
  }

  &__name {
    margin: 0;
    font-family: var(--second-family);
    font-size: 24px;
    line-height: 100%;
    color: var(--white);
  }

  &__hot {
    top: 0;
  }

  &__salary {
    margin: 0;
    font-weight: 500;
    font-size: 18px;
    line-height: 150%;
    color: var(--white);
  }

  &__country {
    margin: 0;
    font-weight: 500;
    font-size: 18px;
    line-height: 150%;
    color: var(--white);
  }

  &__link {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 24px 36px;
    font-family: var(--second-family);
    font-size: 16px;
    line-height: 100%;
    color: var(--yellow);
    border: 1px solid var(--yellow);
    transition: color .5s ease-in-out, background .5s ease-in-out;

    &:hover {
      color: var(--black);
      background-color: var(--yellow);
    }
  }
}
