.cases {
  padding-bottom: 273px;

  &__block {
    padding-top: 178px;

    &-folder {
      display: flex;
      flex-wrap: wrap;
      gap: 20px;
      margin-bottom: 52px;
    }

    &-card {
      display: flex;
      flex-direction: column;
      gap: 49px;
    }

    &-descr {
      display: flex;
      margin-bottom: 20px;
    }

    &-time {
      display: flex;
      flex-direction: column;
      gap: 16px;
      margin-right: 46px;
    }

    &-teh {
      display: flex;
      flex-direction: column;
      gap: 20px;
    }

    &-img {
      display: flex;
      align-items: center;
      gap: 20px;
    }
  }

  &__link {
    &-folder {
      display: flex;
      align-items: center;
      gap: 7px;
      font-family: var(--second-family);
      font-weight: 700;
      font-size: 14px;
      color: var(--text-color);
      transition: color .5s ease-in-out;

      &:hover {
        color: var(--yellow);
      }

      &--icon {
        stroke: #B6B6B6;
        transition: stroke .5s ease-in-out;
      }

      &--active {
        color: var(--yellow);
      }
    }
  }

  &__card {
    position: relative;

    &-name {
      margin: 0;
      margin-bottom: 23px;
      font-weight: 500;
      font-size: 32px;
      line-height: 100%;
      color: var(--white);
    }

    &-descr {
      margin: 0;
      margin-right: 72px;
      max-width: 496px;
      font-weight: 500;
      line-height: 150%;
      color: var(--text-color);
    }

    &-title {
      margin: 0;
      font-family: var(--second-family);
      font-weight: 400;
      font-size: 16px;
      line-height: 100%;
      color: var(--text-color);
    }

    &-time {
      display: flex;
      align-items: center;
      gap: 16px;
      margin: 0;
      font-weight: 500;
      font-size: 24px;
      line-height: 100%;
      color: var(--white);
    }
  }

  &__pagination {
    display: flex;
    gap: 15px;
    margin-top: 45px;

    &-active {
      padding: 13px 20px;
      font-weight: 500;
      font-size: 15px;
      line-height: 100%;
      text-align: center;
      color: var(--yellow);
      background-color: transparent;
      border: 1px solid var(--yellow);
    }
  }
}

.cases__link-folder--active .cases__link-folder--icon {
  stroke: var(--yellow);
}

.cases__link-folder:hover .cases__link-folder--icon {
  stroke: var(--yellow);
}

.pagination__btn {
  position: relative;
  padding: 13px 20px;
  font-weight: 500;
  font-size: 15px;
  line-height: 100%;
  text-align: center;
  color: var(--white);
  background-color: transparent;
  border: 1px solid var(--black);
}

.pagination__btn.cases__pagination-active {
  color: var(--yellow); /* Цвет текста активной кнопки */
  background-color: transparent;
  border: 1px solid var(--yellow); /* Цвет границы активной кнопки */
}
