.form {
  display: flex;
  flex-direction: column;
  gap: 32px;

  &__label {
    position: relative;
    display: flex;
    flex-direction: column;
    gap: 24px;
    font-size: 18px;
    line-height: 100%;
    color: var(--text-color);
  }

  &__input {
    padding-bottom: 10px;
    width: 540px;
    font-size: 24px;
    color: var(--white);
    background-color: transparent;
    border-bottom: 1px solid #272727;
    outline: none;
    transition: border .3s ease-in-out;

    &:focus {
      border-bottom: 1px solid var(--yellow);
    }

    &:hover {
      border-bottom: 1px solid var(--white);
    }

    &:active {
      border-bottom: 1px solid var(--yellow);
    }

    &-file {
      position: relative;
      display: none;

      &::before {
        content: attr(data-file-name); /* Отображаем имя файла */
        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
        font-size: 16px;
        color: var(--white);
      }
    }
  }

  &__text {
    padding: 0;
    max-height: 40px;
    resize: none;
  }

  &__btn {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 8px;
    margin-top: 11px;
    max-width: 198px;
    min-height: 56px;
    font-family: var(--second-family);
    font-size: 17px;
    line-height: 100%;
    color: var(--yellow);
    border: 1px solid var(--yellow);
    box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
    transition: color .5s ease-in-out, background .5s ease-in-out;

    &:hover {
      color: #1f1f1f;
      background-color: var(--yellow);

      .arrow {
        stroke: #1f1f1f;
      }
    }
  }
}


.form__input-file-wrapper {
  position: relative;
  display: flex;
  align-items: center;
  gap: 10px;
  padding-bottom: 15px;
  border-bottom: 1px solid #272727;
  transition: border .3s ease-in-out;

  &:focus {
    border-bottom: 1px solid var(--yellow);
  }

  &:hover {
    border-bottom: 1px solid var(--white);
  }

  &::after {
    content: '';
    position: absolute;
    top: 3px;
    right: 0;
    width: 20px;
    height: 20px;
    background-image: url(../img/file.svg);
    background-repeat: no-repeat;
  }
}

.form__input-file-btn {
  display: none;
  background-color: var(--yellow);
  color: var(--white);
  padding: 8px 12px;
  border: none;
  cursor: pointer;
}

.file-name {
  font-family: var(--second-family);
    font-weight: 400;
    font-size: 12px;
    color: #1f1f1f;
    height: 24px;
    min-width: 87px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 25px;
    background: #e0e0e0;
    padding: 6px 12px;
    opacity: 0;
    transition: opacity .3s ease-in-out;
}

.error-message {
  display: block;
  position: absolute;
  top: 35px;
  right: 0;
  padding-right: 26px;
  min-height: 20px;
  font-size: 12px;
  color: red;
  background-image: url(../img/Info_icon.svg);
  background-repeat: no-repeat;
  background-position: right;
}
