.burger {
  position: relative;
  display: flex;
  align-items: center;
  gap: 16px;
  padding-right: 7px;
  font-family: var(--second-family);
  font-weight: 700;
  font-size: 18px;
  line-height: 100%;
  text-transform: uppercase;
  color: var(--white);
  z-index: 26;

  &__line {
    position: relative;
    top: -4px;
    display: flex;
    width: 30px;
    height: 2px;
    background: var(--white);
    transition: transform .5s ease-in-out;

    &::after {
      content: '';
      position: absolute;
      top: 7px;
      left: 0;
      width: 17px;
      height: 2px;
      background: var(--white);
      transition: transform .5s ease-in-out;
    }

  }

  &.active .burger__line {
    transform: rotate(45deg);
    top: 0;

    &::after {
      transform: rotate(-90deg);
      top: 0;
      width: 30px;
    }
  }

  &__wrapper {
    background-image: url('../img/Background/burger-bg.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }

  &__block {
    position: relative;
    top: 70px;
    display: flex;
    gap: 59px;

    &-left {
      min-width: 606px;
    }
  }

  &__text {
    transition: opacity .5s ease-in-out;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #0c0c0c;
    transform: translateX(-100%); /* Скрываем за экраном */
    transition: transform 0.3s ease;
    z-index: 25;
  }

  &__link {
    font-family: var(--second-family);
    font-size: 32px;
    line-height: 100%;
    color: var(--white);
    transition: color .5s ease;

    &-drop {
      margin-bottom: 16px;
    }
  }

  &__link:hover {
    background: linear-gradient(to left, yellow 50%, var(--white) 75%);
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 32px;
    margin-bottom: 100px;
    max-width: 616px;
  }

  &__dropdown {
    &-list {
      display: flex;
      flex-wrap: wrap;
      gap: 16px 24px;
      margin-top: 16px;
    }

    &-link {
      font-weight: 500;
      font-size: 18px;
      color: var(--white);
      transition: color .5s ease-in-out;

      &:hover {
        color: var(--yellow);
      }
    }
  }
}

/* Меню открывается */
.burger.active + .burger__menu {
  transform: translateX(0);
}
