.project {
  padding-bottom: 134px;

  &__content {
    display: flex;
    flex-direction: column;
    gap: 47px;
    margin-bottom: 49px;
  }

  &__card {
    position: relative;
    display: flex;
    justify-content: flex-end;
  }

  &__name {
    margin: 0;
  height: auto; /* Вместо фиксированной высоты */
  font-weight: 500;
  font-size: 32px;
  line-height: 100%;
  text-align: right;
  color: var(--white);
  transform: rotate(-90deg);
  position: absolute; /* Позиционируем элемент */
  top: 76px; /* Привязываем к верхней части */
  left: 0; /* Настройка горизонтального позиционирования */
  transform-origin: top left;

  &-two {
    top: 128px;
  }

  &-three {
    top: 143px;
  }

  &-four {
    top: 210px;
  }

  }

  &__link {
    position: relative;

    &:hover {
      div {
        opacity: 1;
      }

      &::after {
        opacity: 1;
      }

      &::before {
        opacity: 1;
      }

      .project__link-view {
        opacity: 1;
      }

    }

    div {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.6);
      opacity: 0;
      transition: opacity .5s ease-in-out;
    }

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 4px;
      height: 100%;
      background-color: var(--yellow);
      opacity: 0;
      transition: opacity .5s ease-in-out;
    }

    &::before {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 4px;
      background-color: var(--yellow);
      opacity: 0;
      transition: opacity .5s ease-in-out;
      z-index: 2;
    }

    &-view {
      position: absolute;
      top: 47%;
      left: 40%;
      display: flex;
      align-items: center;
      gap: 6px;
      font-family: var(--second-family);
      font-size: 17px;
      line-height: 100%;
      color: var(--yellow);
      z-index: 3;
      opacity: 0;
      transition: opacity .5s ease-in-out;
    }

    &-more {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 12px;
      margin-left: 66px;
      width: 1045px;
      min-height: 100px;
      font-family: var(--second-family);
      font-size: 17px;
      line-height: 100%;
      color: var(--yellow);
      border: 1px solid var(--yellow);
    }
  }
}
