.services {
  padding-bottom: 110px;

  &__content {
    display: flex;
    flex-wrap: wrap;
    gap: 30px;
  }

  &__card {
    max-width: 350px;

    &-name {
      margin: 0;
      margin-top: 25px;
      margin-bottom: 25px;
      font-family: var(--second-family);
      font-weight: 700;
      font-size: 24px;
      line-height: 100%;
      color: var(--white);
    }

    &-descr {
      margin: 0;
      margin-bottom: 32px;
      font-weight: 500;
      font-size: 17px;
      line-height: 150%;
      color: var(--text-color);
    }

    &-link {
      display: flex;
      align-items: center;
      gap: 8px;
      font-family: var(--second-family);
      font-size: 17px;
      line-height: 100%;
      color: var(--yellow);
    }
  }
}
