.privacy {
  padding-bottom: 127px;

  &__block {
    padding-top: 156px;

    &-back {
      margin-bottom: 52px;
      padding-left: 189px;
    }

    &-info {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-bottom: 72px;
    }

    &-policy {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  }

  &__info {
    &-block {
      max-width: 730px;

      &:not(:last-child) {
        margin-bottom: 52px;
      }
    }

    &-title {
      margin: 0;
      margin-bottom: 52px;
      max-width: 730px;
      font-weight: 500;
      font-size: 52px;
      line-height: 130%;
      color: var(--white);

      &--policy {
        margin-bottom: 35px;
      }
    }

    &-subtitle {
      margin: 0;
      margin-bottom: 24px;
      font-weight: 500;
      font-size: 24px;
      line-height: 100%;
      color: var(--white);
    }

    &-descr {
      margin: 0;
      font-weight: 500;
      font-size: 18px;
      line-height: 150%;
      color: var(--white);

      &--bot {
        margin-bottom: 24px;
      }
    }

  }

  &__list {
    margin: 0;
    margin-bottom: 24px;

    &-bot {
      margin-bottom: 7px;
    }
  }

  &__item {
    position: relative;

    &::before {
      content: '';
      position: absolute;
      top: 20px;
      left: 0;
      width: 8px;
      height: 8px;
      border-radius: 100%;
      background-color: var(--yellow);
    }

    &-none {
      &::before {
        display: none;
      }
    }
  }

  &__text {
    margin: 0;
    padding-left: 22px;
    font-weight: 500;
    font-size: 18px;
    line-height: 250%;
    color: var(--white);

    &-lh {
      line-height: 150%;
    }

    span {
      color: var(--yellow);
    }
  }

  &__link {
    &-mail {
      line-height: 150%;
      text-decoration: underline;
      text-decoration-skip-ink: none;
      color: var(--yellow);
    }
  }
}
