.nav {
  display: flex;
  margin-right: 49px;

  &__list {
    display: flex;
    align-items: center;
  }

  &__item {
    &:not(:last-child) {
      margin-right: 42px;
    }

    &-min {
      &:not(:last-child) {
        margin-right: 44px;
      }
    }
  }

  &__link {
    position: relative;
    display: flex;
    align-items: center;
    font-weight: 500;
    font-size: 18px;
    color: var(--white);
    transition: color .5s ease-in-out;

    &::before {
      content: '';
      position: absolute;
      bottom: -9px;
      left: 0;
      width: 100%;
      height: 1px;
      background-color: var(--yellow);
      opacity: 0;
      transition: opacity .5s ease-in-out;
    }

    &:focus {
      color: var(--yellow);
    }

    &:hover {
      color: var(--yellow);

      &::before {
        opacity: 1;
      }
    }

    &-drop {
      gap: 8px;

      img {
        transition: transform .5s ease-in-out;
        transform: rotate(0deg);
      }

      &--none {
        img {
          display: none;
        }
      }
    }
  }
}

.hot {
  position: relative;
  top: -5px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1px 3px;
  width: 30px;
  height: 16px;
  font-weight: 600;
  font-size: 12px;
  text-transform: uppercase;
  color: #000;
  background: var(--yellow);
}

.dropdown {

  &__list {
    position: absolute;
    top: 105px;
    width: 236px;
    background: #0c0c0c;
    padding: 16px 0 16px 20px;
    max-height: 0;
    overflow: hidden;
    opacity: 0;
    visibility: hidden;
    transition: max-height 0.3s ease, opacity 0.3s ease, visibility 0.3s ease;

    &-none {
      display: none;
    }
  }

  &__item {
    &:not(:last-child) {
      margin-bottom: 24px;
    }
  }

  &__link {
    position: relative;
    font-weight: 500;
    font-size: 18px;
    color: var(--white);
    transition: color .5s ease-in-out;

    &::after {
      content: '';
      position: absolute;
      bottom: -8px;
      left: 0;
      width: 100%;
      height: 1px;
      background-color: var(--yellow);
      opacity: 0;
      transition: opacity .5s ease-in-out;
    }

    &:focus {
      color: var(--yellow);
    }

    &:hover {
      color: var(--yellow);

      &::after {
        opacity: 1;
      }
    }
  }
}

/* Плавное отображение при наведении */
.nav__item:hover .dropdown__list {
  max-height: 500px; /* Установи максимальное значение в зависимости от количества элементов */
  opacity: 1;
  visibility: visible;
}

/* При наведении на ссылку "Expertise" стрелка разворачивается вверх */
.nav__item:hover .nav__link-drop img {
  transform: rotate(180deg);
}
