.header {
  padding-top: 43px;

  &__block {
    display: flex;
    justify-content: space-between;
    align-items: center;

    &-nav {
      display: flex;
    }
  }

  &__logo {
    position: relative;
    padding-top: 9px;
    padding-left: 7px;
    width: 147px;
    height: 68px;
    z-index: 26;
  }

  &__order {
    display: flex;
    align-items: center;
    gap: 8px;
    margin-right: 50px;
    padding: 15px 26px;
    font-family: var(--second-family);
    font-size: 17px;
    line-height: 100%;
    color: var(--yellow);
    border: 1px solid var(--yellow);
    box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
    transition: color .5s ease-in-out, background .5s ease-in-out;

    &:focus {
      color: #000;
      background-color: var(--yellow);
    }

    &:hover {
      color: #000;
      background-color: var(--yellow);

      .arrow {
        stroke: #000;
      }
    }
  }

  &__absolut {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 59px;
    z-index: 5;
  }
}

.arrow {
  stroke: #FDDD0A;
  transition: stroke .5s ease-in-out;
}
