.contacts {
  padding-bottom: 236px;

  &__block {
    display: flex;
    justify-content: space-between;

    &-left {
      max-width: 540px;
    }

    &-right {
      max-width: 445px;

      &--contact {
        position: relative;
        bottom: 67px;
        max-width: 100%;
        width: 540px;
      }
    }

    &-phone {
      display: flex;
      flex-direction: column;
      gap: 20px;
      margin-bottom: 42px;
    }

    &-socials {
      display: flex;
      gap: 16px;
    }

    &-vacancies {
      padding-bottom: 125px;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 32px;
    font-family: var(--second-family);
    font-weight: 700;
    font-size: 32px;
    line-height: 130%;
    color: var(--white);
  }

  &__address {
    margin: 0;
    margin-bottom: 42px;
    font-weight: 500;
    font-size: 18px;
    line-height: 100%;
    color: var(--text-color);
  }

  &__phone {
    font-size: 24px;
    line-height: 100%;
    color: var(--white);
    transition: color .5s ease-in-out;

    &:hover {
      color: var(--yellow);
    }
  }

  &__socials {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 32px;
    height: 32px;
    border-radius: 100%;
    background-color: #1a1a1a;
    transition: background .5s ease-in-out;

    &:hover {
      background-color: var(--yellow);

      .contacts__icon {
        fill: #1a1a1a;
      }
    }
  }

  &__icon {
    fill: var(--white);
    transition: fill .5s ease-in-out;
  }

  &__career {
    padding-bottom: 258px;
  }

  &__vacancies {
    padding-bottom: 62px;
  }

  &__other {
    padding-bottom: 132px;
  }
}
