.vacancies {
  position: relative;
  padding-bottom: 130px;

  &__block {
    padding-top: 158px;

    &-back {
      position: relative;
      margin-bottom: 44px;

      &--meda {
        margin-bottom: 134px;
      }

      &--saltmine {
        margin-bottom: 194px;
      }
    }

    &-title {
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      margin-bottom: 50px;
    }

    &-descr {
      display: flex;
      gap: 30px;
      margin-bottom: 5px;
    }

    &-offer {
      max-width: 540px;
    }

    &-requirements {
      max-width: 445px;
    }

    &-conditions {
      display: flex;
      gap: 30px;
      margin-bottom: 24px;
    }

    &-img {
      display: flex;
      gap: 30px;
    }

    &-contact {
      &--info {
        position: relative;
        top: -5px;
        display: flex;
        flex-direction: column;
        gap: 30px;
        padding: 30px 30px 26px 17px;
        background: var(--yellow);
      }

      &--top {
        display: flex;
        justify-content: space-between;
      }

      &--pos {
        display: flex;
        flex-direction: column;
        gap: 16px;
      }

      &--name {
        margin: 0;
        font-family: var(--second-family);
        font-weight: 700;
        font-size: 24px;
        line-height: 130%;
        color: #0c0c0c;
      }

      &--position {
        margin: 0;
        font-weight: 500;
        line-height: 100%;
        color: #0c0c0c;
      }

      &--message {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 12px;
        padding: 20px 24px;
        min-width: 207px;
        font-family: var(--second-family);
        font-size: 17px;
        line-height: 100%;
        color: var(--black);
        border: 1px solid var(--black);
        transition: color .5s ease-in-out, background .5s ease-in-out;

        &:hover {
          color: var(--yellow);
          background-color: var(--black);

          .message {
            fill: var(--yellow);
          }
        }
      }

      &--bottom {
        display: flex;
        align-items: center;
        gap: 134px;
      }

      &--phone {
        font-size: 24px;
        line-height: 100%;
        color: #0c0c0c;
      }

      &--socials {
        display: flex;
        gap: 16px;
      }
    }
  }

  &__link {
    &-back {
      display: flex;
      align-items: center;
      gap: 16px;
      font-family: var(--second-family);
      font-weight: 700;
      font-size: 18px;
      line-height: 100%;
      color: var(--white);
    }
  }

  &__title {
    position: relative;
    display: flex;
    margin: 0;
    font-family: var(--second-family);
    font-weight: 700;
    font-size: 52px;
    color: var(--white);
  }

  &__hot {
    top: 0px;
    left: 2px;
  }

  &__apply {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 8px;
    padding: 18px 24px;
    font-family: var(--second-family);
    font-size: 17px;
    line-height: 100%;
    color: var(--yellow);
    border: 1px solid var(--yellow);
    box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
    transition: color .5s ease-in-out, background .5s ease-in-out;

    &:hover {
      color: var(--black);
      background-color: var(--yellow);

      .arrow {
        stroke: var(--black);
      }
    }
  }

  &__offer {
    &-title {
      margin: 0;
      margin-bottom: 32px;
      font-weight: 600;
      font-size: 32px;
      line-height: 100%;
      color: var(--white);
    }
  }

  &__location {
    &-title {
      margin: 0;
      margin-bottom: 12px;
      min-width: 255px;
      font-weight: 600;
      font-size: 16px;
      line-height: 150%;
      color: var(--white);
    }

    &-name {
      margin: 0;
      font-weight: 600;
      font-size: 20px;
      line-height: 150%;
      color: var(--yellow);
    }
  }

  &__info {
    &-title {
      margin: 0;
      margin-bottom: 12px;
      font-weight: 600;
      font-size: 18px;
      line-height: 150%;
      color: var(--white);
    }

    &-list {
      margin: 0;
      padding-left: 18px;
      font-size: 16px;
      color: var(--white);

      &:not(:last-child) {
        margin-bottom: 24px;
      }
    }

    &-text {
      margin: 0;
      font-weight: 500;
      font-size: 16px;
      line-height: 150%;
      color: var(--white);
    }

    &-descr {
      margin: 0;
      font-weight: 500;
      font-size: 14px;
      line-height: 150%;
      color: var(--white);
    }
  }

  &__input {
    width: 445px;
  }

  &__btn {
    margin: 0;
    max-width: 124px;
  }
}

.message {
  fill: var(--black);
  transition: fill .5s ease-in-out;
}
