// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Gilroy", sans-serif;
  --content-width: 1110px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  --content-field-width: 1788px;
  --container-field-offset: 27px;
  --container-field-width: calc(var(--content-field-width) + (var(--container-field-offset) * 2));

  // colors
  --yellow: #fddd0a;
	--white: #fff;
	--black: #212121;
	--text-color: #b6b6b6;
  --second-family: "RoadRadio", sans-serif;
  --third-family: "Roboto", sans-serif;
  --font3: "Montserrat", sans-serif;
  --font4: "Exo", sans-serif;
}
