.hero {
  padding-bottom: 607px;

  &__block {
    margin-bottom: 9px;
    padding-top: 56px;
    padding-left: 229px;
    max-width: 1065px;

    &-nav {
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
    }
  }

  &__nav {
    &-down {
      position: relative;
    right: -68px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 53px;
    height: 53px;
    border: 1px solid var(--white);
    border-radius: 100%;
    background-image: url(../img/arrow-bottom.svg);
    background-repeat: no-repeat;
    background-position: center;

    &--thank {
      right: 0;
    }

    &--error {
      right: 0;
    }

    }
  }

  &__name {
    display: flex;
    margin-bottom: 12px;
    padding-left: 110px;
    font-family: var(--second-family);
    font-weight: 700;
    font-size: 24px;
    text-transform: uppercase;
    color: var(--yellow);
  }

  &__help {
    margin: 0;
    padding-left: 270px;
    font-family: var(--second-family);
    font-weight: 900;
    font-size: 140px;
    line-height: 100%;
    color: transparent; /* Делаем текст прозрачным */
    -webkit-text-stroke: 1px white; /* Устанавливаем белую обводку */
    text-stroke: 1px white;
  }

  &__ideas {
    margin: 0;
    padding-left: 110px;
    font-family: var(--second-family);
    font-weight: 900;
    font-size: 140px;
    line-height: 100%;
    color: var(--white);
  }

  &__change {
    margin: 0;
    padding-left: 270px;
    font-family: var(--second-family);
    font-weight: 900;
    font-size: 140px;
    line-height: 100%;
    color: var(--white);
  }

  &__world {
    margin: 0;
    margin-bottom: 6px;
    font-family: var(--second-family);
    font-weight: 900;
    font-size: 140px;
    line-height: 100%;
    color: transparent; /* Делаем текст прозрачным */
    -webkit-text-stroke: 1px white; /* Устанавливаем белую обводку */
    text-stroke: 1px white;
  }

  &__link {
    position: relative;
    padding-right: 9px;
    z-index: 26;

    img {
      animation: spin 5s linear 0s infinite;
    }

    &-career {
      position: absolute;
      right: 60px;
      bottom: 60px;
    }

    &-vacancies {
      bottom: 365px;
    }
  }
}

@keyframes spin {
  from {
      transform: rotate(0deg);
  }

  to {
      transform: rotate(360deg);
  }
}
