*,
*::before,
*::after {
  box-sizing: border-box;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

.page__body {
  margin: 0;
  min-width: 360px;
  min-height: 100%;
  font-size: 16px;
  background: #0c0c0c;
}

.page__body-home {
  background-image: url(../img/Background/home-bg.png);
  background-size: cover;
  background-repeat: no-repeat;
}

.page__body-thank {
  background-image: url(../img/Background/Thankyou-bg.png);
  background-repeat: no-repeat;
  background-position: top 25px left;
}

.page__body-career {
  background-image: url(../img/Background/career-bg.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top 25px center;
}

.page__body-vacancies {
  background-image: url(../img/Background/vacancies-bg.png);
  background-repeat: no-repeat;
  background-position: top 25px left 0px;
}

.page__body-privacy {
  background-image: url(../img/Background/privacy-bg.png);
  background-repeat: no-repeat;
  background-position: top 601px left 0px;
}

.page__body-cases {
  background-image: url(../img/Background/cases-bg.png);
  background-repeat: no-repeat;
  background-position: top 634px left 0px;
}

.page__body-meda {
  background-image: url(../img/Background/meda-bg.png);
  background-repeat: no-repeat;
  background-position: top 1124px left 0px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.container__field {
  margin: 0 auto;
  padding: 0 var(--container-field-offset);
  max-width: var(--container-field-width);
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.btn {
  cursor: url('../img/cursor.png'), auto;
}

.title__min {
  margin: 0;
  height: 46px;
  font-family: var(--second-family);
  font-weight: 700;
  font-size: 52px;
  color: var(--white);
}

.title__big {
  margin: 0;
  font-family: var(--second-family);
  font-weight: 700;
  font-size: 100px;
  color: transparent; /* Делаем текст прозрачным */
  -webkit-text-stroke: 1px white; /* Устанавливаем белую обводку */
  text-stroke: 1px white;
  opacity: 0.1;
}

/* Класс для блокировки скролла */
.body--no-scroll {
  overflow: hidden;
}
