.error {
  padding-bottom: 214px;

  &__block {
    &-title {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-bottom: 5px;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 308px;

    &-info {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  }

  &__img {
    margin-bottom: 37px;
  }

  &__title {
    height: 100%;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 72px;
    line-height: 130%;
    text-align: center;
    color: var(--white);
  }

  &__info {
    position: absolute;
    top: 584px;
    margin: 0;
    max-width: 730px;
    font-weight: 500;
    font-size: 20px;
    line-height: 150%;
    text-align: center;
    color: var(--white);
  }

  &__link {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 12px;
    margin-bottom: 109px;
    padding: 15px 26px;
    font-family: var(--second-family);
    font-size: 17px;
    line-height: 100%;
    color: var(--yellow);
    border: 1px solid var(--yellow);
    box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
    transition: color .5s ease-in-out, background .5s ease-in-out;

    &:hover {
      background-color: var(--yellow);
      color: var(--black);

      .arrow {
        stroke: var(--black);
      }
    }
  }
}
