.stack {
  padding-bottom: 136px;

  &__table {
    &:not(:last-child) {
      margin-bottom: 32px;
    }
  }

  &__name {
    margin: 0;
    margin-bottom: 32px;
    font-family: var(--second-family);
    font-weight: 700;
    font-size: 24px;
    line-height: 100%;
    color: var(--white);
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    gap: 30px;
  }

  &__item {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 160px;
    min-height: 123px;
    border: 1px solid var(--black);

    &:hover {
      &::after {
        opacity: 1;
      }

      &::before {
        opacity: 1;
      }
    }

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 4px;
      height: 100%;
      background-color: var(--yellow);
      opacity: 0;
      transition: opacity .5s ease-in-out;
    }

    &::before {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 4px;
      background-color: var(--yellow);
      opacity: 0;
      transition: opacity .5s ease-in-out;
    }
  }

  &__link {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 17px;
    font-weight: 500;
    font-size: 17px;
    line-height: 100%;
    text-align: center;
    color: var(--white);

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
}

