.industry {
  padding-bottom: 137px;

  &__list {
    display: flex;
    flex-wrap: wrap;
    gap: 30px;
  }

  &__item {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 255px;
    min-height: 134px;
    border: 1px solid var(--black);

    &:hover {
      &::after {
        opacity: 1;
      }

      &::before {
        opacity: 1;
      }
    }

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 4px;
      height: 100%;
      background-color: var(--yellow);
      opacity: 0;
      transition: opacity .5s ease-in-out;
    }

    &::before {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 4px;
      background-color: var(--yellow);
      opacity: 0;
      transition: opacity .5s ease-in-out;
    }
  }

  &__link {
    max-width: 159px;
    font-family: var(--second-family);
    font-weight: 700;
    font-size: 24px;
    line-height: 130%;
    text-align: center;
    color: var(--white);

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
}
