.reviews {
  padding-bottom: 318px;

  &__block {
    &-social {
      display: flex;
      align-items: center;

      img {
        margin-right: 16px;
      }
    }

    &-rating {
      display: flex;
      flex-direction: column;
      gap: 8px;
      margin-right: 30px;
    }

    &-stars {
      display: flex;
      gap: 5px;
      margin: 0;

      img {
        margin: 0;
      }
    }

    &-btn {
      position: relative;
      display: flex;
      justify-content: flex-end;
      gap: 30px;
      max-width: 1102px;
    }

    &-category {
      display: flex;
      align-items: center;
      gap: 24px;
      margin-bottom: 24px;
    }

    &-descr {
      max-width: 445px;
    }
  }

  &__slide {
    margin-bottom: 44px;
  }

  &__content {
    display: flex;
    justify-content: space-between;
    align-items: center;

    &-left {
      max-width: 540px;
    }
  }

  &__position {
    display: inline-flex;
    margin-bottom: 20px;
    font-family: var(--second-family);
    font-weight: 700;
    font-size: 15px;
    line-height: 100%;
    color: var(--yellow);
  }

  &__name {
    margin: 0;
    margin-bottom: 24px;
    font-weight: 700;
    font-size: 32px;
    line-height: 100%;
    color: var(--white);
  }

  &__descr {
    margin: 0;
    margin-bottom: 50px;
    font-weight: 500;
    font-size: 17px;
    line-height: 150%;
    color: var(--text-color);
  }

  &__date {
    margin: 0;
    font-weight: 500;
    font-size: 14px;
    line-height: 100%;
    color: var(--text-color);
  }

  &__pag {
    display: flex;
    align-items: center;
    gap: 17px;
    max-width: 300px;
  }

  &__btn {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 56px;
    height: 56px;
    border-radius: 100%;
    border: 1px solid #5e5e5e;

    &:hover {
      &::after {
        opacity: 1;
      }

      .reviews__icon {
        stroke: var(--yellow);
      }
    }

    &-next {
      transform: rotate(180deg);

      &:after {
        transform: rotate(195deg);
        left: 0px;
      }
    }

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 28px;
      height: 51px;
      background-image: url(../img/reviews/hover-bg.png);
      opacity: 0;
      transition: opacity .5s ease-in-out;
      transform: rotate(-165deg);
    }
  }

  &__icon {
    stroke: #5E5E5E;
    transition: stroke .3s ease-in-out;

    &:hover {
      stroke: var(--yellow);
    }
  }

  &__other {
    &-title {
      margin: 0;
      margin-bottom: 24px;
      font-weight: 700;
      font-size: 42px;
      line-height: 100%;
      color: var(--white);
    }

    &-descr {
      margin: 0;
      margin-bottom: 32px;
      font-weight: 500;
      font-size: 17px;
      line-height: 150%;
      color: var(--text-color);
    }

    &-link {
      display: flex;
      align-items: center;
      gap: 8px;
      font-family: var(--second-family);
      font-size: 17px;
      line-height: 100%;
      color: var(--yellow);
    }
  }
}


.swiper-pagination-bullet {
  width: 10px;
  height: 10px;
  border-radius: 100%;
  border: 1px solid #272727;
}

.swiper-pagination-bullet-active {
  position: relative;
  width: 24px;
  height: 24px;
  border-radius: 100%;
  border: 1px solid #272727;
  background-color: transparent;
  background-image: url(../img/reviews/progress-bg.png);
  background-position: center;
  background-repeat: no-repeat;
}

.other {
  padding-bottom: 252px;
}
