.meda-info {
  padding-top: 70px;
  padding-bottom: 137px;

  &__width {
    max-width: 730px;

    &-title {
      margin: 0;
      margin-bottom: 51px;
      font-family: var(--second-family);
      font-weight: 700;
      font-size: 52px;
      color: var(--white);
    }
  }

  &__block {
    display: flex;
    justify-content: space-between;
    gap: 83px;

    &-tabs {
      display: flex;
      flex-direction: column;
      gap: 24px;
    }

    &-projects {
      display: flex;
      gap: 46px;


      &--left {
        display: flex;
        flex-direction: column;
        gap: 42px;
      }

      &--right {
        display: flex;
        flex-direction: column;
        gap: 42px;
      }

      &--brand {
        display: flex;
        flex-direction: column;
        gap: 16px;
      }
    }

    &-teh {
      display: flex;
      flex-wrap: wrap;
      gap: 26px 45px;
      max-width: 340px;
    }

    &-image {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      margin-bottom: 97px;
    }

    &-bottom {
      justify-content: flex-end;
    }

    &-site {
      display: flex;
      flex-direction: column;
      gap: 18px;
    }
  }

  &__tabs {

    &-link {
      display: flex;
      align-items: center;
      gap: 12px;
      font-family: var(--second-family);
      font-weight: 700;
      font-size: 24px;
      line-height: 100%;
      color: var(--white);
      transition: color .5s ease-in-out, display .5s ease-in-out;

      img {
        display: none;
        transition: display .5s ease-in-out;
      }

      &:hover {
        color: var(--yellow);

        img {
          display: block;
        }
      }
    }
  }

  &__projects {
    margin-bottom: 65px;

    &-title {
      margin: 0;
      margin-bottom: 46px;
      font-family: var(--second-family);
      font-weight: 700;
      font-size: 52px;
      color: var(--white);
    }

    &-subtitle {
      margin: 0;
      font-family: var(--second-family);
      font-weight: 700;
      font-size: 16px;
      line-height: 100%;
      color: var(--text-color);
    }

    &-stars {
      display: flex;
      gap: 5px;
    }
  }

  &__name {
    display: flex;
    align-items: center;
    gap: 13px;
    margin: 0;
    max-width: 334px;
    font-weight: 500;
    font-size: 24px;
    line-height: 100%;
    color: var(--white);
  }

  &__teh {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    min-height: 52px;
    max-width: 59px;

    span {
      font-weight: 500;
      font-size: 12px;
      line-height: 100%;
      text-align: center;
      color: var(--white);
    }
  }

  &__story {
    margin-bottom: 63px;
    max-width: 730px;

    &-top {
      display: flex;
      align-items: center;
      gap: 30px;
      margin-bottom: 32px;
    }

    &-descr {
      margin: 0;
      font-weight: 500;
      font-size: 20px;
      line-height: 150%;
      color: var(--text-color);
    }
  }

  &__user {
    margin-bottom: 70px;
  }

  &__design {
    margin-bottom: 74px;

    &-preview {
      margin-bottom: 47px;
    }
  }

  &__video {
    position: relative;
    margin-bottom: 52px;
    width: 100%;
    height: 624px;
    overflow: hidden;
    z-index: 5;

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(12, 12, 12, 0.3);
      z-index: 3;
    }

    &-play {
      position: relative;
      width: 100%;
      height: 624px;
      object-fit: cover; /* Заполняем контейнер видео */
      display: none;
    }
  }

  &__preview {
    width: 100%;
    height: 100%;
    object-fit: cover; /* Заполняем контейнер превью */
    position: absolute;
    top: 0;
    left: 0;
    z-index: 3;
  }

  &__btn {
    &-play {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 96px;
      height: 96px;
      border-radius: 100%;
      border: none;
      background-color: var(--white);
      z-index: 4;

      &::after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        width: 16px;
        height: 20px;
        transform: translate(-50%, -50%);
        background-image: url(../img/play-video.svg);
        background-position: center;
        background-repeat: no-repeat;
      }
    }
  }

  &__stack {
    margin-bottom: 69px;

    &-bottom {
      display: flex;
      align-items: center;
      gap: 49px;
    }
  }

  &__development {
    margin-bottom: 70px;
  }

  &__result {
    margin-bottom: 65px;

    &-name {
      margin: 0;
      font-family: var(--second-family);
      font-weight: 700;
      font-size: 16px;
      line-height: 100%;
      color: var(--text-color);
    }

    &-descr {
      margin-bottom: 53px;
    }

    &-link {
      display: flex;
      align-items: center;
      gap: 10px;
      font-weight: 500;
      font-size: 24px;
      line-height: 100%;
      color: var(--white);
    }
  }

  &__review {

    &-block {
      display: flex;
      justify-content: space-between;

      &--left {
        margin-top: 66px;
        max-width: 348px;
      }
    }

    &-brands {
      display: block;
      margin-bottom: 20px;
      font-family: var(--second-family);
      font-weight: 700;
      font-size: 15px;
      line-height: 100%;
      color: var(--yellow);
    }

    &-name {
      margin: 0;
      margin-bottom: 24px;
      font-weight: 700;
      font-size: 32px;
      line-height: 100%;
      color: var(--white);
    }

    &-descr {
      margin: 0;
      margin-bottom: 24px;
      font-weight: 500;
      font-size: 17px;
      line-height: 150%;
      color: var(--text-color);
    }
  }
}
