.cookie {

  &__notice {
    display: none;
  justify-content: space-between;
  align-items: center;
	position: fixed;
	z-index: 9999999;
	bottom: 0;
	left: 0;
	right: 0;
	background: #0c0c0c;
	padding: 13px 50px;
	border: 1px solid var(--black);
  }

  &__block {
    &-name {
      display: flex;
      align-items: center;
      gap: 50px;
    }

    &-btn {
      display: flex;
      align-items: center;
      gap: 30px;
    }
  }

  &__title {
    margin: 0;
    font-weight: 700;
    font-size: 18px;
    line-height: 100%;
    color: var(--white);
  }

  &__subtitle {
    margin: 0;
    font-weight: 500;
    font-size: 14px;
    line-height: 100%;
    color: var(--text-color);
  }

  &__btn {
    &-info {
      font-family: var(--second-family);
      font-weight: 400;
      font-size: 14px;
      line-height: 100%;
      text-decoration: underline;
      text-decoration-skip-ink: none;
      text-align: right;
      color: var(--white);
      transition: text-decoration .3s ease-in-out;

      &:hover {
        text-decoration: none;
      }
    }

    &-accept {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 11px 20px;
      font-family: var(--second-family);
      font-size: 14px;
      line-height: 100%;
      text-align: right;
      color: var(--yellow);
      border: 1px solid var(--yellow);
      box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
      transition: color .5s ease-in-out, background .5s ease-in-out;

      &:hover {
        color: var(--black);
        background-color: var(--yellow);
      }
    }
  }
}
