.thank-hero {
  padding-bottom: 214px;

  &__block {
    display: flex;
    justify-content: center;
    padding-top: 216px;

    &-title {
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-bottom: 176px;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;

    &-info {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  }

  &__img {
    position: relative;
    left: -30px;
    margin-bottom: 58px;
  }

  &__title {
    margin-bottom: 35px;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 72px;
    line-height: 130%;
    text-align: center;
    color: var(--white);
  }

  &__info {
    position: absolute;
    top: 584px;
    margin: 0;
    font-weight: 500;
    font-size: 20px;
    line-height: 130%;
    text-align: center;
    color: var(--white);
  }
}
